.books.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(10em, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 10px;
}
  
.books.gallery > a {
    height: max-content;
}

.books.gallery > a, .books.gallery > a > img {
    width: 10em;
}

.book.img {
    position: fixed;
    right: 0;
    top: 0;
    height: 100vh; 
    width: 50vw;
    background-size: cover;
}

.book.overlay {
    height: 100%;
    width: 100%;
    background-image: linear-gradient(to right, 
        rgba(255,255,255,0.99), 
        rgba(255,255,255,0.7), 
        rgba(255,255,255,0.1));
}

.book.img > img {
    height: 100vh;
}