.font {
  font-family: 'Julius Sans One', sans-serif;
}

body {
  font-family: 'Crimson Text', sans-serif;
  font-size: 18px;
}

h1, h2, h3 {
  font-family: 'Julius Sans One', sans-serif;
  margin: 0;
}

.clickable:hover {
  text-decoration: underline;
  cursor: pointer;
}

#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
}

.page {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 50vw;
  max-width: 1000px;
  min-width: 500px;
  margin-top: 100px;
}

.container {
  margin-top: -36px;
  margin-left: 150px;
  z-index: 2;
  position: relative;
  width: 100%;
}

a {
  text-decoration: underline;
  color: black;
}

a:hover {
  color: rgba(0,0,0,0.25);
  text-decoration: underline;
}

a > img:hover {
  filter: brightness(90%);
}

.center-contents {
  width: 100%;
  display: flex;
  justify-content: center;
}